import BreedBox from './BreedBox.js';
import Pokemon from './Pokemon.js';
import PokeView from './PokeView.js';
import * as lsHelpers from './ls.js';
import * as utils from './utilities.js';

var firstParent = null;
var secondParent = null;

export default class PokeBreeder {
    constructor(parent) {
        this.parent = document.getElementById(parent);
        this.body = null;
        this.backBtn = null;
        this.head = null;
        this.key = this.parent.id;
        this.breedBox = new BreedBox(parent);
        this.pokeList = new Pokemon;
        this.pokeView = new PokeView;
    }
    init(){
        //for testing ONLY
        localStorage.clear();
        //for testing ONLY
        
        console.log('successfully loaded PokeBreeder');
        this.parent.innerHTML = '';
        this.parent.classList.add(this.parent.id);
        this.pokeView.renderStructure(this.parent);
        this.head = document.getElementById('mainHead');
        this.body = document.getElementById('mainBody');
        this.backBtn = document.getElementById('backBtn');
        this.saveListeners(this.body);
        this.breedBox.init(this.key);
        this.viewTypes();
    }
    //function to show pokemon types
    async viewTypes(){
        const typeList = await this.pokeList.getAllTypes();
        this.pokeView.renderTypeList(typeList, this.body);
        this.evListeners('typeList');
        this.buildBackButton(1);
        this.buildNextButton(1);
        this.updateCount();
    }

    //function to show a list of pokemon when type is selected
    async showPokemon(thisType, url){
        let filteredTypeList = lsHelpers.readFromLS(thisType);
        if (filteredTypeList == null){
            this.body.innerHTML = `<h3>Loading ${utils.capitalizeName(thisType)} types...</h3>`
            //retrieve basic pokemon info
            const listThisType = await this.pokeList.getDetails(url);
            let thisTypePokemon = [];
            listThisType.pokemon.forEach(item => thisTypePokemon.push(item));
            //figure out which pokemon we actually want to display
            filteredTypeList = [];
            for(let i=0; i < thisTypePokemon.length; i++){
                //get second set of details
                thisTypePokemon[i].full_details = await this.pokeList.getDetails(thisTypePokemon[i].pokemon.url);
                //see if it's an ID we want
                if(thisTypePokemon[i].full_details.id < 805){
                    thisTypePokemon[i].species_details = await this.pokeList.getDetails(thisTypePokemon[i].full_details.species.url);
                    //see if it's breedable and save that info
                    thisTypePokemon[i].full_details.breedable_status = this.checkBreedableStatus(thisTypePokemon[i].species_details);
                    //make sure it has a valid returned value
                    if(thisTypePokemon[i].full_details.breedable_status != null){
                        //this is a Pokemon we want to keep. Save relevant information as an object and push it to an array.
                        let saveThisPokemon = this.breedBox.createPokeObject(thisTypePokemon[i]);
                        filteredTypeList.push(saveThisPokemon);
                    }
                }
            }
            console.log(filteredTypeList);
            lsHelpers.writeToLS(thisType, filteredTypeList);
            this.pokeView.renderPagedList(thisType, utils.sortByName(filteredTypeList), this.body);
        } else {
            this.pokeView.renderPagedList(thisType, utils.sortByName(filteredTypeList), this.body);
        }
    }
    evListeners(parent){
        const parentElement = document.getElementById(parent);
        parentElement.addEventListener('touchend', e => {
            this.showPokemon(e.target.dataset.id, e.target.dataset.url);
            this.backBtn.classList.remove('hidden');
          });
    }

    saveListeners(parent){
        parent.addEventListener('touchend', e => {
            if (e.target.matches(`#pokeList, #pokeList *`)){
                let count = this.breedBox.getCurrentCount();
                if (count < 12){
                    if(e.target.dataset.gender && e.target.dataset.gender != 'd') {
                    //console.log(`add ${e.target.dataset.gender} pokemon ${e.target.dataset.id}`);
                    this.breedBox.addToBox(e.target.dataset.type, e.target.dataset.id, e.target.dataset.gender);
                    this.pokeView.renderBasicList('savedList', this.breedBox.getSavedList());
                    this.updateCount();
                    this.listListeners();
                }} else {
                    this.pokeView.noAdd('full');
                }
            } else if (e.target.matches(`#partnerList, #partnerList *`)){
                let parentTwo = lsHelpers.readFromLS('partners').find(poke => poke.id == e.target.dataset.id);
                    parentTwo.gender = e.target.dataset.gender;
                    secondParent = parentTwo;
                    parent.removeChild(document.getElementById('partnerList'));
                    this.pokeView.renderParentOne(parent, firstParent)
                    this.pokeView.renderParentTwo(parent, secondParent);
                    //invoke this after both parents are chosen
                    this.buildNextButton(3);
            }
        })
    }
    listListeners(){
        const removeBtns = Array.from(document.querySelectorAll('.remove'));
        removeBtns.forEach(btn =>{
            btn.addEventListener('touchend', e => {
                this.breedBox.removeFromBox(e.target.dataset.id, e.target.dataset.gender);
                this.pokeView.renderBasicList('savedList', this.breedBox.getSavedList());
                this.updateCount();
                this.listListeners();
            })
        })
    }
    parentOneListeners(){
        const parentOne = Array.from(document.querySelectorAll('p.fancy-parent'));
        parentOne.forEach(pokeParent => {
            pokeParent.addEventListener('touchend', e => {
                //set global variable for first parent
                firstParent = this.breedBox.getSavedList().find(poke => (poke.id == e.currentTarget.dataset.id && poke.gender == e.currentTarget.dataset.gender));
                this.pokeView.renderParentOne(this.body, firstParent);
                //check if parent is genderless(only Ditto breedable)
                if(firstParent.breed_status === 1){
                    this.breedWithDitto(this.body, firstParent);
                    this.buildBackButton(2);
                    this.backBtn.classList.remove('hidden');
                } else{
                    this.findPartners(this.body, firstParent, firstParent.egg_groups);
                    this.buildBackButton(2);
                    this.backBtn.classList.remove('hidden');
                }
            })
        })
        //console.log(parentOne);
    }

    async findPartners(parent, parentOne, groupArray){
        const sortedList = [];
        const heading = document.createElement('h3');
                heading.innerHTML = `Fetching potential partners...`;
                parent.appendChild(heading);
        //check one or more egg groups to see if they exist and pull or create that data
        for(let x=0; x < groupArray.length; x++) {
            console.log(`egg group ${groupArray[x].name}`);
            //check to see if this egg group's Pokemon have already been saved to LS
            let filteredEggGroupList = lsHelpers.readFromLS(`eg_${groupArray[x].name}`);
            //if group doesn't exist, save it
            if (filteredEggGroupList == null){
                //retrieve basic pokemon info
                const listThisGrp = await this.pokeList.getDetails(groupArray[x].url);
                //push to an array so we can iterate over the species available
                let thisGrpPokemon = [];
                listThisGrp.pokemon_species.forEach(item => thisGrpPokemon.push(item));
                //figure out which pokemon we actually want to display
                filteredEggGroupList = [];
                for(let i=0; i < thisGrpPokemon.length; i++){
                    //get second set of details
                    thisGrpPokemon[i].species_details = await this.pokeList.getDetails(thisGrpPokemon[i].url);
                    thisGrpPokemon[i].full_details = await this.pokeList.getBasicPokemonByID(thisGrpPokemon[i].species_details.id);
                    //check breedable status and sort out ditto-only breeders
                    thisGrpPokemon[i].full_details.breedable_status = await this.checkBreedableStatus(thisGrpPokemon[i].species_details);
                    if (thisGrpPokemon[i].full_details.breedable_status > 1){
                        //Save relevant information as an object and push it to an array.
                        let saveThisPokemon = this.breedBox.createPokeObject(thisGrpPokemon[i]);
                        filteredEggGroupList.push(saveThisPokemon);
                    }
                } 
                filteredEggGroupList.push(this.breedBox.getDittoObject());
                lsHelpers.writeToLS(`eg_${groupArray[x].name}`, filteredEggGroupList);
                //push each Pokemon in the egg group into the main list to be sorted and displayed
                filteredEggGroupList.forEach(savedMon => {
                    //make sure the pokemon isn't already in the array (for pokemon with multiple egg groups)
                    const checkDuplicate = sortedList.some(poke => poke.id == savedMon.id);
                    console.log(checkDuplicate);
                    if(!checkDuplicate){sortedList.push(savedMon);}
                })
            } else {
                //push each Pokemon in the egg group into the main list to be sorted and displayed
                filteredEggGroupList.forEach(savedMon => {sortedList.push(savedMon)});
            }
        }
        //once the sorted list has been populated with pokemon from ALL available egg groups, send it to the list to be rendered  
        heading.innerHTML = `Select Parent 2`;
        lsHelpers.writeToLS('partners',sortedList);
        this.pokeView.renderPartnerList(utils.sortByName(sortedList), parentOne, parent);
    }

    breedWithDitto(parentEl, parentOne){
        const dittoArr = []
        dittoArr[0] = this.breedBox.getDittoObject();

        const heading = document.createElement('h3');
            heading.innerHTML = `Select Parent 2`;
            lsHelpers.writeToLS('partners',dittoArr);
            parentEl.appendChild(heading);
        this.pokeView.renderPartnerList(dittoArr, parentOne, parentEl);
    }

    buildBackButton(state = 1) {
        console.log(`build back button state ${state}`)
        this.backBtn.innerHTML = '';
        const backButton = document.createElement("button");
        if(state == 1){
            this.backBtn.classList.add('hidden');
            backButton.textContent = "<< Back to Types List";
            backButton.addEventListener('touchend', () => {
                this.viewTypes();
                this.buildNextButton(1);
                backButton.classList.add('hidden');
            });
        } else if(state == 2){
            console.log('back to parents');
            backButton.textContent = "<< Back to Parent List";
            backButton.addEventListener('touchend', () => {
                this.pokeView.renderParentList(this.body, this.breedBox.getSavedList());
                this.parentOneListeners();
                this.buildNextButton(2)
                this.backBtn.classList.add('hidden');
            });
        }
        this.backBtn.appendChild(backButton);
    }

    buildNextButton(step = 1){
        const nextBtnDiv = document.getElementById('nextBtn');
        const nextBtn = document.createElement('button');
        if(step == 1){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'Select Parent 1 >>';
            nextBtn.ontouchend = () => {
                nextBtnDiv.innerHTML = '';
                this.pokeView.renderParentList(this.body, this.breedBox.getSavedList());
                this.parentOneListeners();
                this.backBtn.classList.remove('hidden');
            }
            nextBtnDiv.appendChild(nextBtn);
        } else if (step == 2) {
            nextBtnDiv.innerHTML = '';
        } else if (step == 3){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'View Results >>';
            nextBtn.ontouchend = () => {
                this.determineBaby();
                //this.pokeView.renderResults(this.body, firstParent, secondParent, babyMon);
                nextBtnDiv.innerHTML = '';
            }
            nextBtnDiv.appendChild(nextBtn);
        } else if (step == 4){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'Start Over?';
            nextBtn.ontouchend = () => {
                this.init();
            }
        }
    }

    updateCount(){
        let count = this.breedBox.getCurrentCount();
        this.pokeView.showCounter(count);
    }

    checkBreedableStatus(pokeInfo){
        if(pokeInfo){
          //check to see if each pokemon is in the "not breedable" group
          const notBreedable = pokeInfo.egg_groups.filter(group => group.name == ('no-eggs'));
          //check to see if the pokemon can have a gender or is neutral
          const genderLess = pokeInfo.gender_rate;
          if(notBreedable.length != 0 || pokeInfo.name == 'ditto'){
            //console.log(`${oneMon.name} cannot breed with any Pokemon.`);
            return 0;
          } else if (genderLess == -1 && notBreedable.length == 0){
            //console.log(`${oneMon.name} can only breed with Ditto.`);
            return 1;
          } else {
            //console.log(`${oneMon.name} has both genders and is breedable.`);
            return 2;
          }
        } else {
          return null;
        }
    }

    async determineBaby(){
        this.backBtn.classList.add('hidden');
        this.buildNextButton(4);
        this.body.innerHTML = '';
        let evChain;
        //if first parent is genderless (breeding with Ditto), baby's species is the other parent regardless of gender
        if (firstParent.gender == 'genderless' || firstParent.gender == 'female' || secondParent.name == 'ditto'){
            evChain = firstParent.ev_chain;
        } else if (firstParent.gender == 'male'){
            evChain = secondParent.ev_chain;
        }
        evChain = await this.pokeList.getDetails(evChain);
        console.log(evChain);
        let babyInfo = evChain.chain.species;
            babyInfo.baby = evChain.chain.is_baby;
            babyInfo.species_details = await this.pokeList.getDetails(babyInfo.url);
            babyInfo.full_details = await this.pokeList.getBasicPokemonByID(babyInfo.species_details.id);

        if(evChain.baby_trigger_item != null){
            //create two babies and call render function
            babyInfo.trigger_item = await this.pokeList.getDetails(evChain.baby_trigger_item.url);
            babyInfo = this.breedBox.createBabyObject(babyInfo);

            let secondBaby = evChain.chain.evolves_to[0].species;
            secondBaby.baby = false;
            secondBaby.species_details = await this.pokeList.getDetails(secondBaby.url);
            secondBaby.full_details = await this.pokeList.getBasicPokemonByID(secondBaby.species_details.id);
            secondBaby = this.breedBox.createBabyObject(secondBaby);
            console.log(babyInfo);
            console.log(secondBaby);
            this.pokeView.renderResults(this.body, firstParent, secondParent, babyInfo, secondBaby);
        } else {
            //just create one baby object and call render function
            babyInfo = this.breedBox.createBabyObject(babyInfo);
            this.pokeView.renderResults(this.body, firstParent, secondParent, babyInfo);
            console.log(babyInfo);
        }
    }
}