import { getJSON } from './utilities.js';

// Quake Model
export default class Pokemon {
  constructor() {
    this.baseURL =
      'https://pokeapi.co/api/v2/';
  }

  async getDetails(url){
    return await getJSON(url);
  }

  async getAllPokemon(){
    return await getJSON(`${this.baseURL}pokemon/`);
  }

  async getAllTypes(){
    return await getJSON(`${this.baseURL}type/`);
  }

  async getPokemonById(id){
    return await getJSON(`${this.baseURL}pokemon-species/${id}`);
  }

  async getBasicPokemonByID(id){
    return await getJSON(`${this.baseURL}pokemon/${id}`);
  }
}
