import * as utils from './utilities.js';

const allTypeColors = {
  bug : '#8a970f',
  dark : '#3d2e20',
  dragon : '#735dda',
  electric : '#fbb917',
  fairy : '#db94de',
  fighting : '#7a3220',
  fire : '#c71f01',
  flying : '#6475d3',
  ghost : '#5757a5',
  grass : '#6fc133',
  ground : '#ceaf5a',
  ice : '#6ed4f4',
  normal : '#a8a691',
  poison : '#944795',
  psychic : '#ee4681',
  rock : '#a0843e',
  steel : '#8f8d9f',
  water : '#1f7bdb'
}
//IDs of only-male Pokemon
const maleOnly = [32, 33, 34, 106, 107, 128, 237, 313, 414, 475, 538, 539, 627, 628];
//IDs of only-female Pokemon
const femaleOnly = [29, 113, 115, 124, 241, 242, 314, 413, 416, 478, 488, 548, 549, 630, 669, 670, 671, 729, 758, 761, 762, 763];

export default class PokeView {
    renderStructure(parent){
        const mainHeader = document.createElement('header');
            mainHeader.classList.add('main-head');
            mainHeader.setAttribute('id','mainHead');
        mainHeader.innerHTML = `
            <h2>PokBreeder</h2>
        `;
        parent.appendChild(mainHeader);
        const backBtnDiv = document.createElement('div');
          backBtnDiv.setAttribute('id','backBtn');
          backBtnDiv.setAttribute('class','back-btn');
        parent.appendChild(backBtnDiv);

        const mainBody = document.createElement('div');
            mainBody.classList.add('main-body');
            mainBody.setAttribute('id','mainBody');
        parent.appendChild(mainBody);

        const breedBoxContainer = document.createElement('div');
            breedBoxContainer.setAttribute('id','countBox');
            breedBoxContainer.setAttribute('class','count-box');
            breedBoxContainer.innerHTML = `
            <h4><span id="currentCount"></span> Saved Pokemon <span id="maxCount"></span></h4>
            <div id="savedList" class="saved-list"></div>
            `;
        parent.appendChild(breedBoxContainer);
        breedBoxContainer.addEventListener('touchend', e =>{
          if(!e.target.classList.contains('remove')){
            document.getElementById('countBox').classList.toggle('shift-height');
          }
        });
    }

    renderStepTwo(parent){
      const header = document.createElement('div');
          header.innerHTML = `
              <h3>Select Parent #1</h3>
              <p>Choose a saved pokmon to see their possible breeding partners.
              `;
      parent.innerHTML = '';
      parent.appendChild(header);
    }

    renderPagedList(type, typeArray, parent, page = 1) {
        parent.innerHTML = '';
        //build a list of the pokemon, a button for each one, and append them to the parent.
        const title = document.createElement('h3');
        title.innerHTML = `${type.toUpperCase()} TYPE POKEMON`;

        const sortLinks = document.createElement('div');
            sortLinks.setAttribute('class','sort-links');
            sortLinks.innerHTML = ` 
                <select id="sortLinks"> 
                  <option selected disabled>Sort By...</option>
                  <option value="name-a">Name (ascending)</option>
                  <option value="name-d">Name (descending)</option> 
                  <option value="dex-a">Dex # (ascending)</option>
                  <option value="dex-d">Dex # (descending)</option>
                </select>
                `;
        const noAdd = document.createElement('div');
            noAdd.setAttribute('id','noAdd');
            noAdd.setAttribute('class','error-msg');
            noAdd.innerHTML = 'Your Breeding Box is full! Remove Pokemon or tap Select Parent 1.';
        const newList = document.createElement('ul');
            newList.setAttribute('class','poke-list');
            newList.setAttribute('id','pokeList');

        const pages = this.getPages(typeArray, page);
        console.log(pages);
        
        pages.data.forEach(poke => {
            const listItem = this.renderOneMon(poke, type);
            newList.appendChild(listItem);
        })
        const listButtons = this.renderPageButtons(pages);
        parent.appendChild(title);
        parent.appendChild(sortLinks);
        parent.appendChild(noAdd);
          noAdd.classList.add('hidden');
        parent.appendChild(listButtons);
        parent.appendChild(newList);
        
        listButtons.addEventListener('touchend', e =>{
          if(!e.target.disabled){
          this.renderPagedList(type,typeArray,parent,e.target.dataset.page);}
        })

        sortLinks.addEventListener('change', e => {
          if (e.target.value == 'name-a'){ 
            console.log('sort by name ascending');
            this.renderPagedList(type, utils.sortByName(typeArray, 1), parent, 1);
          } else if (e.target.value == 'name-d'){ 
            console.log('sort by name descending');
            this.renderPagedList(type, utils.sortByName(typeArray, 2), parent, 1);
          } else if (e.target.value == 'dex-a'){
            console.log('sort by id ascending');
            this.renderPagedList(type, utils.sortByID(typeArray, 1), parent, 1);
          } else if (e.target.value == 'dex-d'){
            console.log('sort by id descending');
            this.renderPagedList(type, utils.sortByID(typeArray, 2), parent, 1);
          }
        })
    }

    getPages(items, page) {
      const perPage = 10,
      offSet = (page - 1) * perPage,
      pageItems = items.slice(offSet).slice(0, perPage),
      totalPages = Math.ceil(items.length / perPage);
      
      return {
        page: parseFloat(page),
        perPage: perPage,
        prePage: page - 1 ? page - 1 : null,
        nextPage: (totalPages > page) ? parseFloat(page) + 1 : null,
        totalItems: items.length,
        totalPages: totalPages,
        data: pageItems
      };
    }

    renderPageButtons(pages){
      const btnDiv = document.createElement('div');
        btnDiv.setAttribute('class','page-buttons');
      const prevBtn = document.createElement('button');
        prevBtn.textContent = '<';
        prevBtn.setAttribute('data-page',pages.prePage);
        if(pages.prePage == null){prevBtn.disabled = true};
        btnDiv.appendChild(prevBtn);
      const pageArray = [];
      for(let i = 1; i <= pages.totalPages; i++){
        pageArray.push(i);
      }
      pageArray.forEach(item => {
        let newLink = document.createElement('button');
        newLink.textContent = item;
        newLink.setAttribute('data-page',item);
        if(item == pages.page){
          newLink.classList.add('selected');
          newLink.disabled = true;
        }
        btnDiv.appendChild(newLink);
      })
      const nextBtn = document.createElement('button');
        nextBtn.textContent = '>';
        nextBtn.setAttribute('data-page',pages.nextPage);
        if(pages.nextPage == null){nextBtn.disabled = true};
        btnDiv.appendChild(nextBtn);
      return btnDiv;
    }

    renderOneMon(oneMon, typeFilter){
      let buttonSet = '';
      const li = document.createElement('li');
      const spriteURL = oneMon.img; 
      //set up buttons to add pokemon to breedBox list
      if (oneMon.breed_status == 0){
        //disable Add button (pokemon cannot breed)
        buttonSet = `<button data-gender="d" disabled>N/A</button>`;
      } else if (oneMon.breed_status == 1) {
        //display gender-neutral button
        buttonSet = `<button data-gender="genderless" data-id="${oneMon.id}" data-type="${typeFilter}">Add &#9906;</button>`;
      } else if (oneMon.breed_status == 2) {
          //quick check for single-gender pokemon
          if(femaleOnly.includes(oneMon.id)) {
            //display only Female button
            buttonSet = `<button data-gender="female" data-id="${oneMon.id}" data-type="${typeFilter}">Add &#9792;</button> <button data-gender="d" disabled>N/A</button>`;
          } else if (maleOnly.includes(oneMon.id)) {
            //display only Male button
            buttonSet = `<button data-gender="d" disabled>N/A</button> <button data-gender="male" data-name="${oneMon.name}" data-id="${oneMon.id}" data-type="${typeFilter}">Add &#9794;</button>`;
          } else {
            //display both gender buttons
            buttonSet = `<button data-gender="female" data-id="${oneMon.id}" data-type="${typeFilter}">Add &#9792;</button><button data-gender="male" data-id="${oneMon.id}" data-type="${typeFilter}">Add &#9794;</button>`;
          }
      } else {
        console.log(`breedable status: ${oneMon.breed_status}`);
      }   
      li.innerHTML = `
      <img src="${spriteURL}" alt="${oneMon.name} sprite" width="47px" height="47px" /> 
      &#35;${oneMon.id} ${utils.capitalizeName(oneMon.name)}<p class="button-set">${buttonSet}</p>
      `;
      return li;
    }

    renderTypeList(typeList, parent) {
        parent.innerHTML = '';
        const header = document.createElement('div');
          header.innerHTML = `
              <h3>Select Pokmon for Your Box</h3>
              <p>Save your favorite pokmon to see which pokmon they can breed with.`;
        const newList = document.createElement('ul');
        newList.setAttribute('class','type-list');
        newList.setAttribute('id','typeList');
        typeList.results.forEach(type => {
            let bgColor = Object.entries(allTypeColors).find(item => item[0] === type.name);
            //if the color is in the list of types we want to show, create list item; otherwise, don't. (We don't want the unknown or shadow pokemon)
            if (bgColor){
              bgColor = bgColor[1];
              const li = document.createElement('li');
              li.style.backgroundColor = bgColor;
              li.setAttribute('data-id', type.name);
              li.setAttribute('data-url', type.url);
              li.innerHTML = `${type.name.toUpperCase()}`;
              newList.appendChild(li);
            }
        })
        parent.appendChild(header);
        parent.appendChild(newList);
    }

    showCounter(count){
      const countBox = document.getElementById('currentCount');
      countBox.innerHTML = count;
      const maxBox = document.getElementById('maxCount');
      if(count == 12){
        maxBox.innerHTML = '(MAX)';
        this.noAdd('full');
      } else {
        maxBox.innerHTML = '';
        this.noAdd('not-full');
      }
    }

    noAdd(state){
      const noAddBox = document.getElementById('noAdd');
      if(noAddBox){
        if(state == 'full'){
          noAddBox.classList.remove('hidden');
        } else {
          noAddBox.classList.add('hidden');
        }
      }
    }

    renderBasicList(parentID, lsArray){
      const parent = document.getElementById(parentID);
      parent.innerHTML = '';
      const ul = document.createElement('ul');
      console.log(lsArray);
      lsArray.forEach(pokemon => {
        const oneLi = this.renderBasicListItem(pokemon);
        ul.appendChild(oneLi);
      })
      parent.appendChild(ul);
    }

    renderBasicListItem(oneMon){
      console.log(oneMon);
      const li = document.createElement('li');
      li.innerHTML = `
        <p>#${oneMon.id} ${utils.capitalizeName(oneMon.name)} ${utils.genderSymbol(oneMon.gender)}</p>
        <p data-id=${oneMon.id} data-gender=${oneMon.gender} class="remove" title="Remove Pokemon From List">X</p>
      `;
      return li;
    }

    renderParentList(parent, list){
      this.renderStepTwo(parent);
      const pokeParentDiv = document.createElement('div');
      pokeParentDiv.setAttribute('class','fancy-parent-container');
      
      if(list.length <= 0){
        pokeParentDiv.innerHTML = `<p>You haven't saved any Pokemon yet!</p>`;
      } else {
        console.log(list);
        list.forEach(pokeParent => {
          const p = this.renderFancyParent(pokeParent);
          pokeParentDiv.appendChild(p);
        })
      }
      parent.appendChild(pokeParentDiv);
    }

    renderFancyParent(pokeParent){
      const p = document.createElement('p');
          p.setAttribute('class','fancy-parent');
          p.setAttribute('data-id',pokeParent.id);
          p.setAttribute('data-gender',pokeParent.gender);
          p.innerHTML = `
            <img src="${pokeParent.img}" alt="${pokeParent.name}" class="parent-${pokeParent.gender}" width="96px" height="96px">
            <span class="parent-name">${pokeParent.name.toUpperCase()}</span>
            <span class="gender-symbol">${utils.genderSymbol(pokeParent.gender)}</span>
          `;
      return p;
    }

    renderPartnerList(partnerList, parentOne, parentElement){
      const p2List = document.createElement('ul');
      p2List.setAttribute('id','partnerList');
      partnerList.forEach(partnerMon => {
        const p2Li = this.renderOnePartner(parentOne, partnerMon);
        p2List.appendChild(p2Li);
      })
      parentElement.appendChild(p2List);
    }
    renderParentOne(parentElement, lsInfo){
      console.log('renderParentOne')
      console.log(lsInfo);
      const p1Box = this.renderFullParent(lsInfo, 1);
      parentElement.innerHTML = '';
      parentElement.appendChild(p1Box);
    }
    renderParentTwo(parentElement, lsInfo){
      console.log('renderParentTwo');
      console.log(`rendering ${lsInfo.gender} ${lsInfo.name}`);
      const p2Box = this.renderFullParent(lsInfo, 2);
      parentElement.appendChild(p2Box);
    }

    renderFullParent(parentInfo, parentNumber){
      const parentBox = document.createElement('div');
      parentBox.setAttribute('class',`parent-box`);

      let parentTypes = document.createElement('p');
      parentTypes.setAttribute('class','parent-types');
          parentInfo.types.forEach(oneType => {
            console.log(oneType.type.name);
            let typeLbl = document.createElement('div')
            let bgColor = Object.entries(allTypeColors).find(color => color[0] === oneType.type.name);
                bgColor = bgColor[1];
            typeLbl.classList.add('type-label-sm');
            typeLbl.style.backgroundColor = bgColor;
            typeLbl.innerHTML = `${oneType.type.name.toUpperCase()}`;
            parentTypes.appendChild(typeLbl);
          })

      let eggGroups = document.createElement('p');
          eggGroups.innerHTML = `Egg Groups: `;
          let eggGrpList = document.createElement('ul');
              parentInfo.egg_groups.forEach(group => {
                let eggGrpLi = document.createElement('li');
                eggGrpLi.innerHTML = `${utils.capitalizeName(group.name)}`;
                eggGrpList.appendChild(eggGrpLi);
              })
          eggGroups.appendChild(eggGrpList);
          
      parentBox.innerHTML = `
          <h4 class="parent-number">PARENT ${parentNumber}</h4>
          <img src="${parentInfo.img}" alt="${parentInfo.name}" width="144px" height="144px">
          <h3>${parentInfo.name.toUpperCase()}</h3>
          <p class="parent-gender parent-${parentInfo.gender}">${parentInfo.gender}</p>
        `;
      parentBox.appendChild(parentTypes);
      parentBox.appendChild(eggGroups);
      return parentBox;
    }

    renderOnePartner(parentOne, parentTwo){
      console.log(parentOne);
      console.log(parentTwo);
      let buttonSet = '';
      const li = document.createElement('li');
      const spriteURL = parentTwo.img; 
      //set up buttons to add pokemon to partner breeding list
      if(parentOne.gender == 'genderless' || parentTwo.name == 'ditto'){
            //render Ditto add button
            buttonSet = `<button data-gender="genderless" data-id="${parentTwo.id}">Select</button>`
      } else if((femaleOnly.includes(parentTwo.id) && parentOne.gender == 'female')
        || (maleOnly.includes(parentTwo.id) && parentOne.gender == 'male')) {
            //quick check for single-gender pokemon
            //display N/A button if parents would be same gender
            buttonSet = `<button data-gender="d" disabled>N/A</button>`;
      } else if (parentOne.gender == 'female') {
            //display male select button
            buttonSet = `<button data-gender="male" data-id="${parentTwo.id}">Select</button>`;
      } else if (parentOne.gender == 'male'){
            //display female select button
            buttonSet = `<button data-gender="female" data-id="${parentTwo.id}">Select</button>`;
      }  
      li.innerHTML = `
      <img src="${spriteURL}" alt="${parentTwo.name} sprite" width="47px" height="47px" /> 
      ${utils.capitalizeName(parentTwo.name)}<p class="button-set">${buttonSet}</p>
      `;
      return li;
    }

    renderResults(parentEl, parentOne, parentTwo, babyMon, babyTwo = null){
      const pokeParentDiv = document.createElement('div');
        pokeParentDiv.setAttribute('class','fancy-parent-container');
        parentOne = this.renderFancyParent(parentOne);
        parentTwo = this.renderFancyParent(parentTwo);
      const addDiv = document.createElement('p');
        addDiv.setAttribute('class', 'big-text');
        addDiv.innerHTML = '+';
      const equalsDiv = document.createElement('p');
        equalsDiv.setAttribute('class', 'big-text');
        equalsDiv.innerHTML = '=';
      
      pokeParentDiv.appendChild(parentOne);
      pokeParentDiv.appendChild(addDiv);
      pokeParentDiv.appendChild(parentTwo);
      pokeParentDiv.appendChild(equalsDiv);
        
      const babyDiv = this.renderBabyMon(babyMon);
      
      parentEl.appendChild(pokeParentDiv);
      parentEl.appendChild(babyDiv);
      if(babyTwo){ 
        babyTwo = this.renderBabyMon(babyTwo);
        parentEl.appendChild(babyTwo);
      }
    }
    renderBabyMon(babyMon){
      const babyDiv = document.createElement('div');
      babyDiv.setAttribute('class','baby-container');

      if(babyMon.trigger_item.name != null){
        const triggerItem = document.createElement('p');
        triggerItem.innerHTML = `
          REQUIRES ${babyMon.trigger_item.name.toUpperCase()} <img src="${babyMon.trigger_item.img}" alt="parent holding ${babyMon.trigger_item.name}">
        `;
        babyDiv.appendChild(triggerItem);
      }

      const babyImg = document.createElement('img');
      babyImg.setAttribute('src', babyMon.img);
      babyImg.setAttribute('alt', babyMon.name);
      
      const babyLabel = document.createElement('p');
      babyLabel.setAttribute('class','baby-label');
      babyLabel.innerHTML = `${babyMon.name.toUpperCase()} ${babyMon.baby ? '<span class="is-baby">(Baby)</span>' : ''}`;

      const babyTypes = document.createElement('p');
      babyMon.types.forEach(oneType => {
        let typeLbl = document.createElement('span');
        let bgColor = Object.entries(allTypeColors).find(color => color[0] === oneType.type.name);
            bgColor = bgColor[1];
          typeLbl.classList.add('type-label-sm');
          typeLbl.style.backgroundColor = bgColor;
          typeLbl.innerHTML = `${oneType.type.name.toUpperCase()}`;
        babyTypes.appendChild(typeLbl);
      })

      babyDiv.appendChild(babyImg);
      babyDiv.appendChild(babyLabel);
      babyDiv.appendChild(babyTypes);

      return babyDiv;
    }
}