export function getJSON(url){
    return fetch(url)
        .then(response => {
            if(!response.ok){
                throw Error(response.statusText);
            }else {
                return response.json();
            }
        })
        .catch(function(error) {
            console.log(error);
        });
}

export function sortByName(sortArray, order = 1){
    if(order == 1){
      sortArray.sort((a,b) => (a.name > b.name) ? 1 : -1);
    } else if(order == 2){
      sortArray.sort((a,b) => (a.name < b.name) ? 1 : -1);
    }
    return sortArray;
  }
export function sortByID(sortArray, order = 1){
    if(order == 1){
      sortArray.sort((a,b) => (a.id - b.id));
    } else if (order == 2) {
      sortArray.sort((a,b) => (b.id - a.id));
    }
    return sortArray;
  }

export function capitalizeName(name){
    name = name.charAt(0).toUpperCase() + name.slice(1);
    return name;
  }

export function genderSymbol(gender){
    if(gender == 'female'){
        gender = '&#9792;';
    } else if(gender === 'male'){
        gender = '&#9794;';
    } else if(gender === 'genderless'){
        gender = '&#9906;';
    }
    return gender;
}